.autocomplete {
  width: 125px; }

.autocompleteContainer {
  list-style-type: none;
  margin-left: 0px;
  border-style: solid;
  border-width: 1px;
  width: 150px;
  overflow: hidden;
  padding: 0;
  z-index: 99999; }

.autocompleteContainer li {
  margin-left: 0px; }

.autocompleteContainer li em {
  background: #66FF33;
  font-weight: bold;
  font-style: normal; }

/* unvisited link */
#autocompleteContainer a:link {
  color: #FF0000; }

/* visited link */
#autocompleteContainer a:visited {
  color: #00FF00; }

/* mouse over link */
#autocompleteContainer a:hover {
  color: #FF00FF; }

/* selected link */
#autocompleteContainer a:active {
  color: #0000FF; }
